 .title {
    font-size: 82px;
    font-weight: 900;
    margin-bottom: 60px;
}

.text {
    color: #9297A2;
    font-size: 28px;
    margin-bottom: 60px;
    font-weight: 300;
 }

 .section_link {
    font-weight: bold;
    padding-left: 30px;
    font-size: 28px;
    background: no-repeat url("../img/svg/Arr_for_link.svg");
    background-position: 0px;
    background-size: 20px;
}

.white {
    color: #fff;
}



.about { 
    display: flex;
    height: calc(100vh - 21.4vh - 67px);
    justify-content: space-between;
    position: relative;

    .description {
        @include description;
        width: 540px;

        .title {
            
            margin-bottom: 60px;
        }

        .text {
            margin-top: 0;
            margin-bottom: 211px;
        }

    }

    .viwe {
        height: 115vh;
        left: 45%;
        top: -240px;
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 43.2%;
        background: #0C1723;
        border-radius: 60px;
        border: 25px solid #EEEEEE;
        border-bottom: none;
        z-index: -1;

        .head {
            height: 20%;
            width: 80%;
            background: no-repeat url("../img/Group 7.png");
            background-position: center;
            background-size: contain;
            border-bottom: 2px solid #242E39;

        }

        .chat {
            width: 80%;
            height: 70%;
            background: no-repeat url("../img/Group 4.png");
            background-position: center;
            background-size: contain;
    
        }
        
    }
}

#how_it_works {
    background: #0C1723;
    height: 101vh;
}
.how_it_works {
    height:100%;
    color: #fff;
    position: relative;

    .header_section {
        height: 15vh;
    }

    .content {
        display: flex;
        flex-direction: row;
    }

    .description {
        @include description;
        height: 85vh;

        .how_it_works__title {
            margin-bottom: 0;
        }

        .how_it_works__text {
            margin-bottom: 0;
            margin-top: 2.5vh;
        }

        .how_it_works__selection_link {
            color: white;
            display: inline-block;
            margin-top: 30vh;
            background: no-repeat url("../img/svg/Arr_for_link_white.svg");
            background-position: 0px;
            background-size: 20px;
        }

    }


    .viwe {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 10vh;
        position: absolute;
        left: 45%;
        top:0;
        width: 43.2%;
        height: 84vh;
        border-radius: 0px 0px 80px 80px;
        border: 25px solid #19232F;
        border-top: none;

        .viwe__title {
            padding: 3.6vh 28%;
            font-weight: bold;
            font-size: 28px;
            border-bottom: solid 1px #242E39;
        }

        .featches {
            padding-top: 8.6vh;
            width: 87.3%;

            .feache__item {
                color: #000;
                margin-bottom: 4.9vh;
                position: relative;
                border-radius: 28px;
                padding: 27px 0 27px 100px;

 

                &:nth-child(1){
                    background: no-repeat url("../img/ic_1.png"), #FFFFFF;
                    background-position: 25px center;
                    &::before {
                        content: "1";
                        @include feach_icon;
                    }
                }
                &:nth-child(2){
                    background: no-repeat url("../img/ic_2.png"), #FFFFFF;
                    background-position: 25px center;
                    &::before {
                        content: "2";
                        @include feach_icon;
                    }
                }
                &:nth-child(3){
                    background: no-repeat url("../img/ic_3.png"), #FFFFFF;
                    background-position: 25px center;
                    &::before {
                        content: "3";
                        @include feach_icon;
                    }
                }



                h4 {
                    font-size: 20px;
                }

                p {
                    margin: 10px 0 0 0;
                    font-size: 14px;
                    color: #9297A2;
                }
            }

        }
    }
}

#download {
    background: no-repeat url("../img/svg/main_bg.svg");
    background-size: contain;
    background-position: 85% 0px;
}

.download {
    height: 100vh;
    position: relative;


    .content {
        display: flex;
        flex-direction: row;

    }

    .description {
        @include description;
        width: 535px;

    }

    .viwe {
        height: 50vh;
        width: 50%;
 
    }
}